						@change="onSwitchChange"
						data-field="enableSpeakerInfo"
						color="#667eea"
					/>
				</view>
			</view>
			
			<!-- ‰ΩøÁî®ËØ¥Êòé -->
			<view class="form-section">
				<text class="section-title">‰ΩøÁî®ËØ¥Êòé</text>
				<view class="help-content">
					<text class="help-text">üé™ ÊºîÁ§∫Ê®°ÂºèÔºöÊó†ÈúÄÈÖçÁΩÆÔºåÂèØÁõ¥Êé•‰ΩìÈ™åËØ≠Èü≥ÂΩïÂà∂ÂäüËÉΩ</text>
					<text class="help-text">ÔøΩ ÁÅ´Â±±ÂºïÊìéÔºöÈúÄÁî≥ËØ∑APIÂØÜÈí•ÔºåÊîØÊåÅÁúüÂÆûËØ≠Èü≥ËØÜÂà´</text>
					<text class="help-text">üé§ Âú®ËÅäÂ§©È°µÈù¢ÈïøÊåâËØ≠Èü≥ÊåâÈíÆÂºÄÂßãÂΩïÈü≥</text>
					<text class="help-text">‚ú® ÊùæÂºÄÊåâÈíÆËá™Âä®ËØÜÂà´Âπ∂ËΩ¨Êç¢‰∏∫ÊñáÂ≠ó</text>
				</view>
				
				<view class="error-help" v-if="testResult && !testResult.success">
					<text class="error-title">‚ö†Ô∏è Â∏∏ËßÅÈóÆÈ¢òËß£ÂÜ≥ÊñπÊ°àÔºö</text>
					<text class="error-text">‚Ä¢ grant not found: App IDÊàñAccess KeyÈîôËØØ</text>
					<text class="error-text">‚Ä¢ ÊùÉÈôê‰∏çË∂≥: ËØ∑Á°ÆËÆ§Â∑≤ÂºÄÈÄöËØ≠Èü≥ËØÜÂà´ÊúçÂä°</text>
					<text class="error-text">‚Ä¢ ÁΩëÁªúË∂ÖÊó∂: Ê£ÄÊü•ÁΩëÁªúËøûÊé•Áä∂ÂÜµ</text>
					<text class="error-text">‚Ä¢ Âª∫ËÆÆÂÖàÂ∞ùËØïÊºîÁ§∫Ê®°Âºè‰ΩìÈ™åÂäüËÉΩ</text>
				</view>
				
				<button class="help-btn" @click="openHelp">
					üìñ Êü•ÁúãËØ¶ÁªÜÈÖçÁΩÆÊåáÂçó
				</button>
			</view>
		</view>
		
		<!-- Êìç‰ΩúÊåâÈíÆ -->
		<view class="actions">
			<button class="test-btn" @click="testConfiguration" :disabled="!canTest">
				üß™ ÊµãËØïÈÖçÁΩÆ
			</button>
			<button class="save-btn" @click="saveConfiguration" :disabled="!canSave">
				‚úÖ ‰øùÂ≠òÈÖçÁΩÆ
			</button>
		</view>
		
		<!-- ÊµãËØïÁªìÊûúÊòæÁ§∫ -->
		<view v-if="testResult" class="test-result" :class="testResult.success ? 'success' : 'error'">
			<text class="result-title">{{ testResult.success ? '‚úÖ ÊµãËØïÊàêÂäü' : '‚ùå ÊµãËØïÂ§±Ë¥•' }}</text>
			<text class="result-message">{{ testResult.message }}</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				config: {
					appId: '',
					accessKey: '',
					modelName: 'bigmodel',
					enablePunc: true,
					enableItn: true,
					enableSpeakerInfo: false
				},
				modelOptions: ['bigmodel', 'general'],
				modelIndex: 0,
				testResult: null
			}
		},
		
		computed: {
			canSave() {
				// ÈúÄË¶ÅÈÖçÁΩÆÂÆåÊï¥ÊâçËÉΩ‰øùÂ≠ò
				return this.config.appId.trim() && this.config.accessKey.trim()
			},
			
			canTest() {
				// ÈúÄË¶ÅÈÖçÁΩÆÂÆåÊï¥ÊâçËÉΩÊµãËØï
				return this.config.appId.trim() && this.config.accessKey.trim()
			}
		},
		
		onLoad() {
			this.loadConfiguration()
		},
		
		methods: {
			// Âä†ËΩΩÈÖçÁΩÆ
			loadConfiguration() {
				const saved = uni.getStorageSync('voiceConfig')
				if (saved) {
					this.config = { ...this.config, ...saved }
					
					// ËÆæÁΩÆÊ®°ÂûãÈÄâÊã©Âô®
					const modelIndex = this.modelOptions.indexOf(this.config.modelName)
					this.modelIndex = modelIndex >= 0 ? modelIndex : 0
				}
			},
			
			// Ê®°ÂûãÈÄâÊã©ÂèòÂåñ
			onModelChange(e) {
				this.modelIndex = e.detail.value
				this.config.modelName = this.modelOptions[this.modelIndex]
			},
			
			// ÂºÄÂÖ≥ÂèòÂåñ
			onSwitchChange(e) {
				const field = e.currentTarget.dataset.field
				this.config[field] = e.detail.value
			},
			
			// ÊµãËØïÈÖçÁΩÆ
			async testConfiguration() {
				if (!this.canTest) return
				
				uni.showLoading({
					title: 'ÊµãËØï‰∏≠...'
				})
				
				try {
					// ÁÅ´Â±±ÂºïÊìéAPIÊµãËØï
					await this.testVolcengineMode()
					
				} catch (error) {
					console.error('ÊµãËØïÂ§±Ë¥•:', error)
					let errorMessage = 'Êú™Áü•ÈîôËØØ'
					
					// ËØ¶ÁªÜÁöÑÈîôËØØÂàÜÁ±ª
					if (error.message.includes('grant not found')) {
						errorMessage = 'App ID Êàñ Access Key Êó†ÊïàÔºåËØ∑Ê£ÄÊü•ÈÖçÁΩÆÊòØÂê¶Ê≠£Á°Æ'
					} else if (error.message.includes('timeout')) {
						errorMessage = 'ÁΩëÁªúË∂ÖÊó∂ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
					} else if (error.message.includes('unauthorized') || error.message.includes('403')) {
						errorMessage = 'ÊùÉÈôê‰∏çË∂≥ÔºåËØ∑Á°ÆËÆ§Ë¥¶Âè∑Â∑≤ÂºÄÈÄöËØ≠Èü≥ËØÜÂà´ÊúçÂä°'
					} else if (error.message.includes('400')) {
						errorMessage = 'ËØ∑Ê±ÇÂèÇÊï∞ÈîôËØØÔºåËØ∑Ê£ÄÊü•ÈÖçÁΩÆ‰ø°ÊÅØ'
					} else {
						errorMessage = error.message || 'ÊúçÂä°Ë∞ÉÁî®Â§±Ë¥•'
					}
					
					this.testResult = {
						success: false,
						message: `ÊµãËØïÂ§±Ë¥•Ôºö${errorMessage}`
					}
					
					// ÊòæÁ§∫ËØ¶ÁªÜÁöÑÂ∏ÆÂä©‰ø°ÊÅØ
					uni.showModal({
						title: 'ÊµãËØïÂ§±Ë¥•',
						content: `${errorMessage}\n\nÂª∫ËÆÆÔºö\n1. Á°ÆËÆ§App IDÂíåAccess KeyÊ≠£Á°Æ\n2. Á°ÆËÆ§Â∑≤ÂºÄÈÄöÁÅ´Â±±ÂºïÊìéËØ≠Èü≥ËØÜÂà´ÊúçÂä°\n3. Ê£ÄÊü•ÁΩëÁªúËøûÊé•\n4. ÂèØÂ∞ùËØï‰ΩøÁî®ÊºîÁ§∫Ê®°Âºè‰ΩìÈ™åÂäüËÉΩ`,
						showCancel: false
					})
				} finally {
					uni.hideLoading()
				}
			},
			
			// ÊµãËØïÁÅ´Â±±ÂºïÊìéÊ®°Âºè
			async testVolcengineMode() {
				// ÂÖàËøõË°åÁÆÄÂçïÁöÑAPIËøûÈÄöÊÄßÊµãËØï
				uni.showLoading({
					title: 'Ê£ÄÊü•APIËøûÊé•...'
				})
				
				const connectivityTest = await this.testApiConnectivity()
				
				if (!connectivityTest.success) {
					throw new Error(connectivityTest.message)
				}
				
				// Â∞ùËØïËé∑ÂèñÊµãËØïÈü≥È¢ëÊï∞ÊçÆ
				uni.showLoading({
					title: 'ÂáÜÂ§áÊµãËØïÈü≥È¢ë...'
				})
				
				const audioData = await this.getTestAudioData();
				if (!audioData) {
					// Â¶ÇÊûúÊ≤°ÊúâÈü≥È¢ëÊï∞ÊçÆÔºåÂè™ËøõË°åAPIËøûÈÄöÊÄßÊµãËØï
					this.testResult = {
						success: true,
						message: 'APIÈÖçÁΩÆÊ≠£Á°ÆÔºåËøûÊé•ÊµãËØïÊàêÂäüÔºÅÔºàÊú™ÊâæÂà∞ÊµãËØïÈü≥È¢ëÊñá‰ª∂Ôºå‰ΩÜAPIËøûÈÄöÊÄßÊ≠£Â∏∏Ôºâ'
					}
					
					uni.showToast({
						title: 'APIËøûÊé•ÊàêÂäü',
						icon: 'success'
					})
					return
				}
				
				// ‰ΩøÁî®Èü≥È¢ëÊï∞ÊçÆËøõË°åÂÆåÊï¥ÊµãËØï
				uni.showLoading({
					title: 'Ê≠£Âú®ËØÜÂà´ËØ≠Èü≥...'
				})
				
				// Áõ¥Êé•Ë∞ÉÁî®Flash APIÔºå‰ΩøÁî®audioData
				const result = await this.callFlashAPI(null, audioData)
				
				this.testResult = {
					success: true,
					message: result ? `ËØÜÂà´ÊàêÂäüÔºö${result}` : 'APIÈÖçÁΩÆÊ≠£Á°ÆÔºå‰ΩÜÊµãËØïÈü≥È¢ëÊó†Ê≥ïËØÜÂà´ÂÜÖÂÆπ'
				}
				
				uni.showToast({
					title: 'ÊµãËØïÊàêÂäü',
					icon: 'success'
				})
			},
			
			// APIËøûÈÄöÊÄßÊµãËØï
			async testApiConnectivity() {
				try {
					// ÁÆÄÂçïÁöÑAPIÊµãËØïËØ∑Ê±Ç
					const response = await uni.request({
						url: "https://openspeech.bytedance.com/api/v3/auc/bigmodel/recognize/flash",
						method: 'POST',
						header: {
							"X-Api-App-Key": this.config.appId,
							"X-Api-Access-Key": this.config.accessKey,
							"X-Api-Resource-Id": "volc.bigasr.auc_turbo",
							"X-Api-Request-Id": "test-connectivity",
							"X-Api-Sequence": "-1",
							"Content-Type": "application/json"
						},
						data: {
							user: { uid: "test" },
							audio: { url: "test" },
							request: { model_name: "bigmodel" }
						},
						timeout: 10000
					})
					
					// Ê£ÄÊü•ÂìçÂ∫îÁä∂ÊÄÅ
					if (response.statusCode === 200) {
						const statusCode = response.header['X-Api-Status-Code']
						if (statusCode === '20000000' || statusCode === '40000001') {
							// ËøûÊé•ÊàêÂäüÔºàÂç≥‰ΩøÂèÇÊï∞ÈîôËØØ‰πüËØ¥ÊòéËÆ§ËØÅÈÄöËøáÔºâ
							return { success: true }
						} else if (statusCode === '40000003' || statusCode === '40000004') {
							// ËÆ§ËØÅÂ§±Ë¥•
							return { 
								success: false, 
								message: 'App ID Êàñ Access Key È™åËØÅÂ§±Ë¥•'
							}
						}
					}
					
					return { success: true } // ÂÖ∂‰ªñÊÉÖÂÜµËÆ§‰∏∫ËøûÊé•Ê≠£Â∏∏
				} catch (error) {
					return { 
						success: false, 
						message: `ËøûÊé•Â§±Ë¥•Ôºö${error.message}`
					}
				}
			},
			
			// Ë∞ÉÁî®Flash APIËøõË°åËØ≠Èü≥ËØÜÂà´
			async callFlashAPI(audioUrl, audioData = null) {
				const requestId = this.generateUUID()
				const recognizeUrl = "https://openspeech.bytedance.com/api/v3/auc/bigmodel/recognize/flash"
				
				const headers = {
					"X-Api-App-Key": this.config.appId,
					"X-Api-Access-Key": this.config.accessKey,
					"X-Api-Resource-Id": "volc.bigasr.auc_turbo",
					"X-Api-Request-Id": requestId,
					"X-Api-Sequence": "-1",
					"Content-Type": "application/json"
				}
				
				// ÊûÑÂª∫Èü≥È¢ëÊï∞ÊçÆ
				let audio_data = null
				if (audioUrl) {
					audio_data = { "url": audioUrl }
				} else if (audioData) {
					audio_data = { "data": audioData } // Base64ÁºñÁ†ÅÁöÑÈü≥È¢ëÊï∞ÊçÆ
				} else {
					// Â¶ÇÊûúÊ≤°ÊúâÈü≥È¢ëÊï∞ÊçÆÔºå‰ΩøÁî®‰∏Ä‰∏™ÊûÅÂ∞èÁöÑÊµãËØïÈü≥È¢ëÊï∞ÊçÆ
					// Ëøô‰∏ªË¶ÅÁî®‰∫éÊµãËØïAPIÁöÑËøûÈÄöÊÄßÂíåÊùÉÈôê
					audio_data = { "data": "" }
				}
				
				const requestData = {
					user: {
						uid: this.config.appId
					},
					audio: audio_data,
					request: {
						model_name: this.config.modelName,
						enable_itn: this.config.enableItn,
						enable_punc: this.config.enablePunc,
						enable_ddc: true,
						enable_speaker_info: this.config.enableSpeakerInfo
					}
				}
				
				console.log('Flash API ËØ∑Ê±ÇÊï∞ÊçÆ:', requestData)
				
				const response = await uni.request({
					url: recognizeUrl,
					method: 'POST',
					header: headers,
					data: requestData,
					timeout: 30000
				})
				
				console.log('Flash API ÂìçÂ∫î:', JSON.stringify(response))
				
				if (response.statusCode === 200) {
					const statusCode = response.header['X-Api-Status-Code']
					if (statusCode === '20000000') {
						// ËØÜÂà´ÊàêÂäüÔºåÁõ¥Êé•ËøîÂõûÁªìÊûú
						const result = response.data
						
						// Ëß£ÊûêËØÜÂà´ÁªìÊûú
						let recognizedText = ''
						
						// ÊîØÊåÅÂ§öÁßçËøîÂõûÊ†ºÂºè
						if (result && result.data && result.data.result && result.data.result.text) {
							recognizedText = result.data.result.text
						} else if (result && result.result && result.result.text) {
							recognizedText = result.result.text
						} else if (result && result.utterances && result.utterances.length > 0) {
							recognizedText = result.utterances.map(item => item.text).join('')
						} else if (result && result.text) {
							recognizedText = result.text
						}
						
						return recognizedText.trim() || null
					} else {
						throw new Error(`ËØ≠Èü≥ËØÜÂà´Â§±Ë¥•: ${response.header['X-Api-Message'] || 'Êú™Áü•ÈîôËØØ'}`)
					}
				} else {
					throw new Error(`APIË∞ÉÁî®Â§±Ë¥•: HTTP ${response.statusCode}`)
				}
			},
			
			// ‰øùÂ≠òÈÖçÁΩÆ
			saveConfiguration() {
				if (!this.canSave) return
				
				try {
					uni.setStorageSync('voiceConfig', this.config)
					
					uni.showToast({
						title: '‰øùÂ≠òÊàêÂäü',
						icon: 'success'
					})
					
					// Âª∂ËøüËøîÂõû
					setTimeout(() => {
						uni.navigateBack()
					}, 1500)
					
				} catch (error) {
					uni.showToast({
						title: '‰øùÂ≠òÂ§±Ë¥•',
						icon: 'error'
					})
				}
			},
			
			// ÊâìÂºÄÂ∏ÆÂä©ÊñáÊ°£
			openHelp() {
				uni.showModal({
					title: 'ÁÅ´Â±±ÂºïÊìéËØ≠Èü≥ËØÜÂà´ÈÖçÁΩÆÊåáÂçó',
					content: '1. ËÆøÈóÆ console.volcengine.com\n2. ÂºÄÈÄö"ËØ≠Èü≥ÊäÄÊúØ-ËØ≠Èü≥ËØÜÂà´"ÊúçÂä°\n3. ÂàõÂª∫Â∫îÁî®ÔºåËé∑ÂèñApp ID\n4. Âú®"ËÆøÈóÆÁÆ°ÁêÜ"‰∏≠ÂàõÂª∫Access Key\n5. Á°Æ‰øùË¥¶Âè∑ÊúâË∂≥Â§ü‰ΩôÈ¢ùÊàñÂ∑≤Ë¥≠‰π∞Â•óÈ§ê\n6. Â∞ÜËé∑ÂèñÁöÑApp IDÂíåAccess KeyÂ°´ÂÖ•ÈÖçÁΩÆ\n\nÊ≥®ÊÑèÔºöËØ∑Á°Æ‰øùÊúçÂä°Â∑≤Ê≠£Â∏∏ÂºÄÈÄöÂπ∂Êúâ‰ΩøÁî®ÊùÉÈôê',
					confirmText: 'ÊòéÁôΩ‰∫Ü',
					showCancel: false
				})
			},
			
			// Ëé∑ÂèñÊµãËØïÈü≥È¢ëÊï∞ÊçÆ
			async getTestAudioData() {
				try {
					console.log('ÂºÄÂßãËé∑ÂèñÊµãËØïÈü≥È¢ëÊï∞ÊçÆ...');
					
					// Ëé∑ÂèñstaticÁõÆÂΩï‰∏ãÁöÑtest.mp3Êñá‰ª∂
					const audioPath = '/static/test.mp3';
					
					// #ifdef APP-PLUS
					// AppÁéØÂ¢É‰∏ã‰ΩøÁî®plus.io
					return new Promise((resolve, reject) => {
						console.log('AppÁéØÂ¢ÉÔºöÂ∞ùËØïËØªÂèñÈü≥È¢ëÊñá‰ª∂');
						try {
							const filePath = plus.io.convertLocalFileSystemURL(audioPath);
							plus.io.resolveLocalFileSystemURL(filePath, (entry) => {
								entry.file((file) => {
									const reader = new plus.io.FileReader();
									reader.onloadend = (e) => {
										const base64 = e.target.result.split(',')[1];
										console.log('AppÁéØÂ¢ÉÊàêÂäüËØªÂèñÈü≥È¢ëÊñá‰ª∂:', audioPath);
										resolve(base64);
									};
									reader.onerror = () => {
										console.error('AppÁéØÂ¢ÉËØªÂèñÈü≥È¢ëÊñá‰ª∂Â§±Ë¥•');
										resolve('');
									};
									reader.readAsDataURL(file);
								});
							}, () => {
								console.error('AppÁéØÂ¢ÉÔºöÊñá‰ª∂‰∏çÂ≠òÂú®');
								resolve('');
							});
						} catch (error) {
							console.error('AppÁéØÂ¢ÉËé∑ÂèñÈü≥È¢ëÊñá‰ª∂Âá∫Èîô:', error);
							resolve('');
						}
					});
					// #endif
					
					// #ifndef APP-PLUS
					// ÈùûAppÁéØÂ¢ÉÂ§ÑÁêÜ
					console.log('ÈùûAppÁéØÂ¢ÉÔºöÊ£ÄÊü•ÂèØÁî®ÁöÑAPI');
					
					// Ê£ÄÊü•ÊòØÂê¶ÊîØÊåÅÊñá‰ª∂Á≥ªÁªüÁÆ°ÁêÜÂô®ÔºàÂ∞èÁ®ãÂ∫èÁéØÂ¢ÉÔºâ
					if (typeof uni.getFileSystemManager === 'function') {
						console.log('Â∞èÁ®ãÂ∫èÁéØÂ¢ÉÔºö‰ΩøÁî®getFileSystemManager');
						const fs = uni.getFileSystemManager();
						return new Promise((resolve) => {
							fs.readFile({
								filePath: audioPath,
								encoding: 'base64',
								success: (res) => {
									console.log('Â∞èÁ®ãÂ∫èÁéØÂ¢ÉÊàêÂäüËØªÂèñÈü≥È¢ëÊñá‰ª∂:', audioPath);
									resolve(res.data);
								},
								fail: (error) => {
									console.error('Â∞èÁ®ãÂ∫èÁéØÂ¢ÉËØªÂèñÈü≥È¢ëÊñá‰ª∂Â§±Ë¥•:', error);
									resolve('');
								}
							});
						});
					} 
					// H5ÁéØÂ¢ÉÊàñÂÖ∂‰ªñÁéØÂ¢É
					else if (typeof fetch !== 'undefined') {
						console.log('H5ÁéØÂ¢ÉÔºö‰ΩøÁî®fetch API');
						try {
							const response = await fetch(audioPath);
							if (!response.ok) {
								throw new Error(`HTTP ${response.status}: ${response.statusText}`);
							}
							
							const blob = await response.blob();
							
							return new Promise((resolve) => {
								const reader = new FileReader();
								reader.onload = function() {
									const base64 = reader.result.split(',')[1]; // ÂéªÊéâdata:audio/mp3;base64,ÂâçÁºÄ
									console.log('H5ÁéØÂ¢ÉÊàêÂäüËØªÂèñÈü≥È¢ëÊñá‰ª∂:', audioPath);
									resolve(base64);
								};
								reader.onerror = function() {
									console.error('H5ÁéØÂ¢ÉËØªÂèñÈü≥È¢ëÊñá‰ª∂Â§±Ë¥•');
									resolve('');
								};
								reader.readAsDataURL(blob);
							});
						} catch (error) {
							console.error('H5ÁéØÂ¢ÉËé∑ÂèñÈü≥È¢ëÊñá‰ª∂Â§±Ë¥•:', error);
							console.log('H5ÁéØÂ¢ÉÔºöÊó†Ê≥ïËé∑ÂèñÈü≥È¢ëÊñá‰ª∂ÔºåË∑≥ËøáÈü≥È¢ëÊµãËØï');
							return '';
						}
					} 
					// ÂÖ∂‰ªñÁéØÂ¢É
					else {
						console.log('ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅÊñá‰ª∂ËØªÂèñÔºåË∑≥ËøáÈü≥È¢ëÊµãËØï');
						return '';
					}
					// #endif
					
				} catch (error) {
					console.error('Ëé∑ÂèñÊµãËØïÈü≥È¢ëÊï∞ÊçÆÂá∫Èîô:', error);
					return '';
				}
			},
			
			// ÁîüÊàêUUID
			generateUUID() {
				return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
					const r = Math.random() * 16 | 0
					const v = c === 'x' ? r : (r & 0x3 | 0x8)
					return v.toString(16)
				})
			}
		}
	}
</script>

